@font-face { font-family: 'NanumBarunpen Bold'; src: url(./fonts/NanumBarunpenB.ttf) format('truetype'); }

.Home {
    align-items: center;
    justify-content: center;
    text-align: center;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

.MainImage {
    position: absolute;
    /* background: linear-gradient(#142d60, #02070f); */
    background: linear-gradient(#142d60, #02070f);

    max-width: 100%;
    text-align: center;
}

/* .GaebokImage {
    position: fixed;
    max-width: 16%;
    max-height: auto;

    opacity: 50%;
} */

.FooterImage {
    position: absolute;
    margin-top: 900px;

    text-align: center;
   
    width: 299.7px;
    height: 125.1px;
}

.StartButton {
    position: absolute;
    transform: translate( -50%, 50%);
    margin-top: 250px;

    color: white;
    background: #2e425c;
    padding: 24.3px 59.4px;
    font-size: 35px;
    font-family: 'NanumBarunpen Bold';
    line-height: 1.7;

    border: 6.3px solid #95c8e6;
    border-radius: 47.7px;
    outline: 0;
    box-shadow: 0 0 11.7px #008cff;

    max-width: 100%;
}